@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&family=Poppins:wght@300;400;600&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    --color-bg:#1f1f38;
    --color-bg-varient:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-varient:rgba(77,181,255,0.4);
    --color-white:#fff;
    --color-light:rgba(255,255,255,0.6);
--container-width-lg:75%;
--container-width-md:86%;
--container-width-sm:90%;
--transition-all:all 400ms ease;

}
body{
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
    background-color: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg-texture.png);
}

::-webkit-scrollbar{
    display: none;
}


/* common styles */

.container{
    width:var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size:  2rem;
}
h5{font-size:1rem}
section{
    margin-top: 8rem;
    /* height: 100vh; */
}
section>h2,section>h5{
    color: var(--color-light);
    text-align: center;
}

section>h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: var(--color-light);
}


a{
    color: var(--color-primary);
    transition: var(--transition-all);

}
a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    color: var(--color-primary);
    padding: 5px;
    border-radius: .4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition-all);
    font-size: 1.5rem;

}
.btn:hover{
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background-color: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* for md */

@media screen and (max-width:1024px){
    .container{
        width: var(--container-width-md);
    }
}

@media screen and (max-width:600px) {
    .container{
        width: var(--container-width-sm);
    }
    
}